"use client";

import { useState, useRef, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { X, MessageSquare } from 'lucide-react';
import { ChatCompletionMessage } from './chatInterface';
import createChatCompletion from './createChatcompletion';

export default function ChatPopup() {
  const [isOpen, setIsOpen] = useState(false);
  const [messages, setMessages] = useState<ChatCompletionMessage[]>([]);
  const [message, setMessage] = useState("");

  const bottomRef = useRef<HTMLDivElement>(null);

  const toggleChat = () => {
    setIsOpen(!isOpen);
  };

  const handleMessage = async () => {
    const userMessagesCount = messages.filter(msg => msg.role === "user").length;

    // Check if limit reached
    if (userMessagesCount >= 3) {
      alert("You have reached the maximum number of questions allowed (3).");
      return;
    }

    const updatedMessages = [
      ...messages,
      {
        role: "user",
        content: message,
      },
    ];

    setMessages(updatedMessages);
    setMessage("");

    const memoryMessages: ChatCompletionMessage[] = [
      {
        role: "system",
        content: `
You are a portfolio career assistant chatbot for Hartheek and dont answer any other question other than him.
Hartheek's background:
- Aspiring Software and Data Engineer with expertise in Python, SQL, TypeScript, Node.js, NestJS, Next.js, and Microservices.
- Skilled in building REST APIs, ETL pipelines, and cloud-native solutions using AWS, Docker, Kubernetes, and Terraform.
- Proficient in analytics and data visualization tools such as Pandas, NumPy, Scikit-learn, PySpark, Power BI, Tableau, and Matplotlib.
- Experienced with databases including PostgreSQL, MySQL, and MongoDB, with strong knowledge of Git, CI/CD, and Jenkins.
- IT Intern at Color Coded Kids, enhancing STEM education through web development, dashboarding, and strategic initiatives.
- Former Software Engineer Intern at Netisoft Consultants, focused on microservices architecture, secure API development, and AWS integration.
- Former Data Analyst Intern at Globalshala Pvt. Ltd, specializing in data validation, visualization, and reporting for compliance and decision-making.
- Successfully delivered multiple projects such as CMOâ€™s Healthcare Access platform, ThoughtMatch Web Application, Class Presence Web Application, and CareerAlign Web Application, focusing on healthcare, education, and recruitment domains.
- Holds a Masterâ€™s degree in Information Systems from Saint Louis University (GPA: 3.89/4.0) and a Bachelorâ€™s degree in Information Technology from Jawaharlal Nehru Technological University.
- Demonstrated leadership by organizing seminars and workshops, recruiting and training volunteers for impactful community projects.
Use this information to answer any questions about Hartheek's career.
        `.trim(),
      },
    ];

    const response = (await createChatCompletion([...memoryMessages, ...updatedMessages])).choices[0]?.message;
    setMessages([...updatedMessages, response]);
  };

  useEffect(() => {
    if (bottomRef.current) {
      bottomRef.current.scrollIntoView({ behavior: "smooth" });
    }
  }, [messages]);

  return (
    <div>
      {/* Floating Button */}
      <button
        onClick={toggleChat}
        className="fixed bottom-6 right-6 p-4 bg-blue-600 text-white rounded-full shadow-lg hover:bg-blue-700 focus:outline-none"
      >
        {isOpen ? <X size={24} /> : <MessageSquare size={24} />}
      </button>

      {/* Chatbox Popup */}
      <AnimatePresence>
        {isOpen && (
          <motion.div
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: 50 }}
          className="fixed bottom-20 right-6 w-80 h-[500px] bg-white rounded-lg shadow-2xl flex flex-col overflow-hidden"
        >
        
            {/* Header */}
            <div className="bg-blue-600 text-white px-4 py-2 font-semibold">
              Talk To Me ðŸ’¬
            </div>

            {/* Chat Messages */}
            <div className="flex-1 p-4 overflow-y-auto text-sm space-y-4">
              {/* Welcome Messages */}
              <div className="bg-gray-100 p-3 rounded-md max-w-[75%] self-start">
                <p className="font-semibold text-gray-700 mb-1">Bot:</p>
                <p>Hi there! How can we help you?</p>
              </div>
              <div className="bg-gray-100 p-3 rounded-md max-w-[75%] self-start">
                <p className="font-semibold text-gray-700 mb-1">Bot:</p>
                <p>You can ask anything about Hartheek!</p>
              </div>

              {/* Dynamic Messages */}
              {messages.map((message, index) => (
                <div
                  key={index}
                  className={`flex flex-col ${
                    message.role === "user" ? "items-end" : "items-start"
                  }`}
                >
                  <div
                    className={`${
                      message.role === "user" ? "bg-blue-100" : "bg-gray-100"
                    } p-3 rounded-lg max-w-[75%]`}
                  >
                    <p className="font-semibold text-gray-700 mb-1">
                      {message.role === "user" ? "You:" : "Bot:"}
                    </p>
                    <p>{message.content}</p>
                  </div>
                </div>
              ))}

              {/* Scroll Target */}
              <div ref={bottomRef} />
            </div>

            {/* Input */}
            <div className="border-t p-2">
              <input
                type="text"
                placeholder="Type a message..."
                value={message}
                onChange={(event) => setMessage(event.target.value)}
                onKeyDown={async (event) => {
                  if (event.key === 'Enter') {
                    await handleMessage();
                  }
                }}
                className="w-full border rounded-full px-4 py-2 text-sm focus:outline-none"
                disabled={messages.filter(msg => msg.role === "user").length >= 3}
              />
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}
